import React, { Component } from 'react'
import "../main.css"
import {
    NavLink,

} from "react-router-dom";

let Cookie = require("js-cookie")
let Pubsub = require("pubsub-js")
export default class Header extends Component {
    constructor(props){
        super(props)
        this.state={
            auth:""
        }
    }
    componentDidMount(){
        Pubsub.subscribe("auth",(msg,data)=>{
            this.setState({
                auth:data
            })
        })

    }
    render() {
        // Cookie.remove("auth")
       if(Cookie.get("auth")){
        return (
            <div>
                <nav className="navbar navbar-light">
                    <div className="container">
                        <a className="navbar-brand" href="index.html">conduit</a>
                        <ul className="nav navbar-nav pull-xs-right">
                            <li className="nav-item">

                                <NavLink activeClassName="active" className="nav-link " exact to="/">Home</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="active" className="nav-link" to="/edit">
                                    <i className="ion-compose"></i>&nbsp;New Article
                                </NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="active" className="nav-link" to="/settings">
                                    <i className="ion-gear-a"></i>&nbsp;Settings
                                </NavLink>
                            </li>
                           
                            <li className="nav-item">
                                <NavLink activeClassName="active" className="nav-link ng-binding" to="/profile">
                                <img ng-src="https://z3.ax1x.com/2021/04/30/gAmA00.jpg" className="user-pic" src="https://z3.ax1x.com/2021/04/30/gAmA00.jpg"/>29651223
                                </NavLink>
                            </li>
                        </ul>
                    </div>
                </nav>
            </div>
        )
       }else{
        return (
            <div>
                <nav className="navbar navbar-light">
                    <div className="container">
                        <a className="navbar-brand" href="index.html">conduit</a>
                        <ul className="nav navbar-nav pull-xs-right">
                            <li className="nav-item">

                                <NavLink activeClassName="active" className="nav-link " exact to="/">Home</NavLink>
                            </li>
                           
                            <li className="nav-item">
                                <NavLink activeClassName="active" className="nav-link" to="/login">Sign in</NavLink>
                            </li>
                            <li className="nav-item">
                                <NavLink activeClassName="active" className="nav-link" to="/loginup">Sign up</NavLink>
                            </li>
                        </ul>
                    </div>
                </nav>
            </div>
        )
       }
    }
}
