import React, { Component } from 'react'
let { request } = require("../api/api")
let Cookie = require("js-cookie")
let Pubsub = require("pubsub-js")
class User extends React.Component {
    constructor(props) {
        super(props)
    }
    blur(e) {
        let value = e.target.value
        console.log(value);
        Pubsub.publish("value", value)
    }

    render() {
        return (
            <div>
                <fieldset className="form-group">
                    <input onBlur={this.blur.bind(this)} className="form-control form-control-lg" type="text" placeholder="Your Name" />
                </fieldset>
            </div>
        )
    }
}
export default class Login extends Component {
    constructor(props) {
        super(props)

        this.state = {
            username: "",
            email: "2865402304@qq.com",
            password: "123456789",
            abled: false
        }
        this.onblur1 = this.onblur1.bind(this)
        this.onblur2 = this.onblur2.bind(this)
        console.log(this.props);
    }
    componentDidMount() {
        Pubsub.subscribe("value", (msg, data) => {
            this.setState({
                username: data
            })
        })

    }
    isLogin() {
        return this.props.match.path === "/login"
    }
    submit(e) {
        e.preventDefault()
        this.setState({
            abled: true
        })

        let user = {
            "username": this.state.username ? this.state.username : "",
            "email": this.state.email,
            "password": this.state.password
        }
        request({
            method: "post",
            url: this.state.username ? "/api/users" : "/api/users/login",
            data: {
                user
            }
        }).then(res => {
            Cookie.set("auth", res.data.user.token)

            this.props.history.push("/")
            Pubsub.publish("auth", res.data.user.token)
        })


    }



    onblur1(e) {
        this.setState({
            email: e.target.value
        })
    }
    onblur2(e) {
        this.setState({
            password: e.target.value
        })
    }
    render() {
        console.log(this.state);
        return (
            <div>
                <div className="auth-page">
                    <div className="container page">
                        <div className="row">

                            <div className="col-md-6 offset-md-3 col-xs-12">
                                <h1 className="text-xs-center">
                                    {this.isLogin() ? "Sign in" : "Sign up"}
                                </h1>
                                <p className="text-xs-center">
                                    <a href="">
                                        {this.isLogin() ? "Need an account?" : "Have an account?"}</a>
                                </p>

                                <ul className="error-messages">
                                    <li>That email is already taken</li>
                                </ul>

                                <form onSubmit={this.submit.bind(this)}>
                                    {this.isLogin() ? "" : <User />}
                                    <fieldset className="form-group">
                                        <input onBlur={this.onblur1} className="form-control form-control-lg" type="text" placeholder="Email" />
                                    </fieldset>
                                    <fieldset className="form-group">
                                        <input onBlur={this.onblur2} className="form-control form-control-lg" type="password" placeholder="Password" />
                                    </fieldset>
                                    <button disabled={this.state.abled} className="btn btn-lg btn-primary pull-xs-right">
                                        {this.isLogin() ? "Sign in" : "Sign up"}
                                    </button>
                                </form>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
