import React, { Component } from 'react'
let { addFavorite, delFavorite } = require('../api/favorite.js')
let { request } = require("../api/api")
export default class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: {
                articles: []
            },

            args: [],


            totalpage: 0,
            currentpage: 1,
            offset: 0,
            //数据在加载过程中点赞禁止
            favoritedisabled: false

        }
        this.favorite = this.favorite.bind(this)
    }
    componentDidMount() {
        // 左侧内容
        request({
            method: "get",
            url: "/api/articles",
            params: {
                offset: 0
            }
        }).then(res => {
            console.log(res);
            this.setState({
                data: res.data,
                totalpage: res.data.articlesCount / res.data.articles.length
            })
        })
        //右侧数据 
        request({
            method: "get",
            url: "/api/tags",
            params: {}
        }).then(res => {
            // console.log(res);
            this.setState({
                args: res.data.tags
            })
        })
        //分页器
        // request({
        //     method: "get",
        //     url: "/api/articles",
        //     params: {
        //         offset: 0
        //     }
        // }).then(res => {
        //     console.log(res);
        //     this.setState({
        //         totalpage: res.data.articlesCount / res.data.articles.length
        //         // res.data.articlesCount == 0 ? 0 :
        //         // res.data.articles.length == 0 ? 0 :
        //         //     res.data.articlesCount / res.data.articles.length
        //     })
        // })

    }
    click(k) {
        request({
            method: "get",
            url: "/api/articles",
            params: {
                offset: k * 20
            }
        }).then(res => {

            this.setState({
                data: res.data,
                currentpage: k + 1,

            })
        })
    }


    async favorite(data) {

        this.setState({
            favoritedisabled: true
        })
        if (data.favorited) {
            await delFavorite(data).then((res) => {
                console.log("取消", res);
            })
            data.favorited = false;
            data.favoritesCount += -1;
            let key = this.state.data.articles.indexOf(data);
            this.state.data.articles[key] = data;
            await this.setState({  //异步
                articles: this.state.data.articles
            })
        } else {
            await addFavorite(data).then((res) => {
                console.log("点赞", res);
            })
            data.favorited = true;
            data.favoritesCount += 1;
            let key = this.state.data.articles.indexOf(data);
            this.state.data.articles[key] = data;
            await this.setState({  //异步
                articles: this.state.data.articles
            })
        }
        await this.setState({
            favoritedisabled: false
        })
    }
    render() {
        return (
            <div>
                <div className="home-page">

                    <div className="banner">
                        <div className="container">
                            <h1 className="logo-font">conduit</h1>
                            <p>A place to share your knowledge.</p>
                        </div>
                    </div>

                    <div className="container page">
                        <div className="row">

                            <div className="col-md-9">
                                <div className="feed-toggle">
                                    <ul className="nav nav-pills outline-active">
                                        <li className="nav-item">
                                            <a className="nav-link " href="">Your Feed</a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link active" href="">Global Feed</a>
                                        </li>
                                    </ul>
                                </div>

                                {this.state.data.articles.map((item, index) => {
                                    return (
                                        <div className="article-preview" key={index}>
                                            <div className="article-meta">
                                                <a href="profile.html"><img src={item.author.image} /></a>
                                                <div className="info">
                                                    <a href="" className="author">{item.author.username}</a>
                                                    <span className="date">{item.createdAT}</span>
                                                </div>
                                                <button className="btn btn-outline-primary btn-sm pull-xs-right"
                                                    onClick={() => {
                                                        this.favorite(item)
                                                    }} disabled={this.state.favoritedisabled}
                                                >
                                                    <i className="ion-heart"></i> {item.favoritesCount}
                                                </button>
                                            </div>
                                            <a href="" className="preview-link">
                                                <h1>{item.title}</h1>
                                                <p>{item.description}</p>
                                                <span>Read more...</span>
                                            </a>
                                        </div>

                                    )
                                })}

                            </div>

                            <div className="col-md-3">
                                <div className="sidebar">
                                    <p>Popular Tags</p>

                                    <div className="tag-list">
                                        {this.state.args.map((item, index) => {
                                            return (
                                                <a href="" className="tag-pill tag-default" key={index}>{item}</a>
                                            )
                                        })}

                                    </div>
                                </div>
                            </div>
                            <nav>
                                <ul className="pagination">
                                    {Array.from({ length: this.state.totalpage }, (v, k) => {
                                        return (
                                            <li key={k} className={this.state.currentpage == k + 1 ? "page-item active" : "page-item"} onClick={this.click.bind(this, k)}>
                                                <a className="page-link " >{k + 1}</a>
                                            </li>
                                        )
                                    })}

                                </ul>
                            </nav>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}
