import {
    request
} from "./api";
import Cookie from 'js-cookie';
//点赞
export let addFavorite = (item) => {
    var slug = item.slug;
    //定义一个请求拦截(身份验证)
    request.interceptors.request.use(
        function (config) {
            //携带token   保存到请求头
            config.headers.Authorization =
                "Token " + Cookie.get("auth");
            return config;
        },
        function (error) {

            return Promise.reject(error);
        });

    //发送点赞请求
    return request({
        method: "POST",
        url: `/api/articles/${slug}/favorite`,
    })


}
//取消点赞
export let delFavorite = (item) => {
    var slug = item.slug;
    //定义一个请求拦截
    request.interceptors.request.use(
        function (config) {
            //携带token   保存到请求头
            config.headers.Authorization =
                " Token " + Cookie.get("auth");
            return config;
        },
        function (error) {
            return Promise.reject(error);
        });
    //取消点赞
    return request({
        method: "DELETE",
        url: `/api/articles/${slug}/favorite`
    })


}